import { ChangeDetectionStrategy, Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { Store } from '@ngrx/store';
import * as fromRoot from '../../../../store/index';
import { map } from 'rxjs/operators';

@Component({
   selector: 'app-admin-page-header',
   templateUrl: './admin-page-header.component.html',
   styleUrls: ['./admin-page-header.component.scss'],
   changeDetection: ChangeDetectionStrategy.OnPush,
})
export class AdminPageHeaderComponent implements OnInit {
   url$: Observable<string>;

   pagesData = {
      'exchanges-show': {
         title: ' ',
         linkTo: 'exchanges-create',
      },
      'currencies-show': {
         title: '',
         linkTo: 'currencies-create',
      },
      'orders-show': {
         title: '  ',
      },
      'currencies-create': {
         title: ' ',
      },
      'exchanges-create': {
         title: '  ',
      },
      'currencies-edit': {
         title: ' ',
      },
      'exchanges-edit': {
         title: '  ',
      },
      order: {
         title: '  ',
      },
      'validators-show': {
         title: '',
      },
      'merchants-show': {
         title: '',
      },
   };
   constructor(private store: Store<fromRoot.AppState>) {}

   ngOnInit(): void {
      this.url$ = this.store
         .select(fromRoot.getCurrentUrl)
         .pipe(map((url) => url.split('/')[2].split('?')[0]));
   }
}
